<!-- pages/news-detail/news-detail.vue -->
<template>
  <view class="news-detail">

    <!-- ÊñáÁ´†ÂÜÖÂÆπÂå∫ -->
    <scroll-view scroll-y class="content-scroll">
      <!-- Ê†áÈ¢òÂå∫Âüü -->
      <view class="header-section">
        <text class="news-title">{{ newsDetail.title }}</text>
        <view class="news-meta">
          <view class="meta-item">
            <text class="department">{{ newsDetail.department }}</text>
          </view>
          <view class="meta-item">
            <text class="publish-time">{{ newsDetail.publishTime }}</text>
          </view>
        </view>
        <view class="stats-bar">
          <view class="stat-item">
            <text class="iconfont">üëÅ</text>
            <text class="stat-text">{{ formatNumber(newsDetail.views) }}ÈòÖËØª</text>
          </view>
          <view class="stat-item">
            <text class="iconfont">üëç</text>
            <text class="stat-text">{{ formatNumber(newsDetail.likes) }}</text>
          </view>
        </view>
      </view>

      <!-- Ê≠£ÊñáÂÜÖÂÆπ -->
      <view class="news-content">
        <block v-if="newsDetail.cover">
          <swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000">
            <swiper-item v-for="(item, index) in newsDetail.cover" style="height: 100%;
width: 100%;" :key="index">
              <view class="swiper-item">
                <image :src="item" mode="widthFix" class="cover-image" @click="previewImage(item)">
                </image>
              </view>
            </swiper-item>
          </swiper>
        </block>

        <view class="content-text">
          <text class="paragraph" v-for="(para, index) in newsDetail.content" :key="index">
            {{ para }}
          </text>
        </view>
      </view>

      <!-- Â∫ïÈÉ®‰ø°ÊÅØ -->
      <view class="footer-section">
        <text class="source-text">{{ newsDetail.source }}</text>
        <text class="copyright">Êú¨ÊñáÂÜÖÂÆπÂΩí{{ newsDetail.department }}ÊâÄÊúâ</text>
      </view>
    </scroll-view>

    <!-- Â∫ïÈÉ®Êìç‰ΩúÊ†è -->
    <view class="action-bar">
      <view class="action-input">
        <input type="text" placeholder="ÂÜô‰∏ã‰Ω†ÁöÑËØÑËÆ∫..." class="comment-input" />
      </view>
      <view class="action-buttons">
        <view class="action-btn like" :class="{ active: isLiked }" @tap="toggleLike">
          <text class="iconfont">üëç</text>
          <text class="btn-text">{{ isLiked ? 'Â∑≤ÁÇπËµû' : 'ÁÇπËµû' }}</text>
        </view>
        <view class="action-btn collect" :class="{ active: isCollected }" @tap="toggleCollect">
          <text class="iconfont">‚≠ê</text>
          <text class="btn-text">{{ isCollected ? 'Â∑≤Êî∂Ëóè' : 'Êî∂Ëóè' }}</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        newsId: '',
        isLiked: false,
        isCollected: false,
        newsDetail: {
          title: '„ÄêÂº†Èô¢ÁÉ≠ÁÇπ„ÄëÂº†ÂÆ∂ÁïåÂ≠¶Èô¢2023-2024Â≠¶Âπ¥ÂõΩÂÆ∂Â•ñÂ≠¶ÈáëÂÖ¨Á§∫ÂêçÂçï',
          department: 'Â≠¶ÁîüËµÑÂä©ÁÆ°ÁêÜ‰∏≠ÂøÉ',
          publishTime: '2024-12-19 10:30',
          views: 95000,
          likes: 3280,
          cover: ['https://zhihuiyingxin.oss-cn-hangzhou.aliyuncs.com/59a3d5cf-c37f-404e-8e14-20b04e1c52df.jpg',
            'https://zhihuiyingxin.oss-cn-hangzhou.aliyuncs.com/395ef0e8-3bb7-4ccd-9242-e04435426579.jpg'
          ],
          content: [
            "Ê†πÊçÆ„ÄäÂÖ≥‰∫éÂÅöÂ•Ω2024Âπ¥ÊôÆÈÄöÈ´òÊ†°ÂõΩÂÆ∂Â•ñÂä©Â≠¶ÈáëËØÑÂÆ°Â∑•‰ΩúÁöÑÈÄöÁü•„ÄãÔºàÊπòÂ≠¶Âä©Ôπù2024Ôπû30Âè∑ÔºâÊñá‰ª∂Á≤æÁ•ûÔºåÁªèÂ≠¶ÁîüÊú¨‰∫∫Áî≥ËØ∑„ÄÅÂêÑ‰∫åÁ∫ßÂ≠¶Èô¢Á°ÆÂÆöÂàùÊ≠•Êé®ËçêÂêçÂçï„ÄÅÂ≠¶ÁîüÂ∑•‰ΩúÂ§ÑÂÆ°Ê†∏„ÄÅÂ≠¶Èô¢ÂõΩÂÆ∂Â•ñÂ≠¶ÈáëËØÑÂÆ°È¢ÜÂØºÂ∞èÁªÑÂÆ°ÂÆöÔºåÊúÄÁªàËØÑÂÆöÂá∫2024Âπ¥Âº†ÂÆ∂ÁïåÂ≠¶Èô¢Êú¨ÁßëÁîüÂõΩÂÆ∂Â•ñÂ≠¶ÈáëËé∑Â•ñËÄÖ36ÂêçÔºåÂÖ∑‰ΩìÂêçÂçïËØ¶ËßÅ‰ª•‰∏ãË°®Ê†º„ÄÇ"
          ],
          source: 'Ê†°Âõ≠Êñ∞Èóª‰∏≠ÂøÉ',
        }
      }
    },
    onLoad(options) {
      // Ëé∑ÂèñÈ°µÈù¢ÂèÇÊï∞‰∏≠ÁöÑÊñ∞ÈóªID
      this.newsId = options.id
      // ÂÆûÈôÖÂ∫îÁî®‰∏≠ËøôÈáåÂ∫îËØ•Ê†πÊçÆIDËØ∑Ê±ÇÂêéÁ´ØAPIËé∑ÂèñÊñ∞ÈóªËØ¶ÊÉÖ
      this.getNewsDetail()
    },
    methods: {
      // Ëé∑ÂèñÊñ∞ÈóªËØ¶ÊÉÖ
      getNewsDetail() {
        // ËøôÈáåÊ®°ÊãüAPIËØ∑Ê±ÇÔºåÂÆûÈôÖ‰ΩøÁî®Êó∂ÊõøÊç¢‰∏∫ÁúüÂÆûÁöÑAPIË∞ÉÁî®
        uni.showLoading({
          title: 'Âä†ËΩΩ‰∏≠...'
        })

        setTimeout(() => {
          uni.hideLoading()
        }, 500)
      },


      // ÂàÜ‰∫´Êñ∞Èóª
      shareNews() {
        uni.share({
          provider: "weixin",
          scene: "WXSceneSession",
          type: 0,
          title: this.newsDetail.title,
          success: function(res) {
            console.log("success:" + JSON.stringify(res));
          },
          fail: function(err) {
            console.log("fail:" + JSON.stringify(err));
          }
        });
      },

      previewImage(value) {
        const randomInt = Math.floor(Math.random() * 100);
        uni.previewImage({
          current: randomInt,
          urls: [value]
        })
      },

      // Ê†ºÂºèÂåñÊï∞Â≠ó
      formatNumber(num) {
        if (num >= 10000) {
          return (num / 10000).toFixed(1) + '‰∏á'
        }
        return num
      },

      // ÁÇπËµûÊìç‰Ωú
      toggleLike() {
        this.isLiked = !this.isLiked
        if (this.isLiked) {
          this.newsDetail.likes++
          uni.showToast({
            title: 'ÁÇπËµûÊàêÂäü',
            icon: 'success'
          })
        } else {
          this.newsDetail.likes--
        }
      },

      // Êî∂ËóèÊìç‰Ωú
      toggleCollect() {
        this.isCollected = !this.isCollected
        uni.showToast({
          title: this.isCollected ? 'Êî∂ËóèÊàêÂäü' : 'ÂèñÊ∂àÊî∂Ëóè',
          icon: 'success'
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .news-detail {
    display: flex;
