      }
    }

    .action-buttons {
      display: flex;
      align-items: center;

      .action-btn {
        display: flex;
        align-items: center;
        padding: 8px 12px;
        border-radius: 16px;
        margin-left: 10px;
        background-color: #f5f5f5;

        &.active {
          color: #ff6b6b;
          background-color: #fff2f2;
        }

        .iconfont {
          margin-right: 5px;
        }

        .btn-text {
